.intro {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 800px;
    margin-bottom: 160px;

    color: $primary-color;

    background: $color-content url("../images/intro-bg.jpg") center no-repeat;
    background-size: cover;

    position: relative;

    @include breakpoint($breakpoint-xs) {
        margin-bottom: 24px;
        height: 746px;

        background-image: url('../images/intro-bg-mobile.jpg');
    }

    &__inner {
        width: 100%;
        max-width: 460px;
        display: flex;
        flex-direction: column;

        position: relative;
        z-index: 1;
    }

    &__title {
        margin-bottom: 25px;
        width: 100%;
        max-width: 340px;

        font-size: 20px;
        font-weight: 400;

        order: 2;

        @include breakpoint($breakpoint-xs) {
            margin-bottom: 47px;
            max-width: 230px;

            font-size: 18px;
        }

    }

    &__subtitle {
        margin-bottom: 12px;

        font-family: 'Oswald', sans-serif;
        font-size: 54px;
        font-weight: 700;
        line-height: 1.3;
        letter-spacing: 0.02em;

        order: 1;

        @include breakpoint($breakpoint-xs) {
            margin-top: 35px;
            margin-bottom: 25px;

            font-size: 44px;

            br {
                display: none;
            }
        }
    }

    &__text-desktop {
        @include breakpoint($breakpoint-xs) {
            display: none;
        }
    }

    &__text-mobile {
        display: none;

        @include breakpoint($breakpoint-xs) {
            display: inline;
        }
    }

    &__decorative {
        height: 98px;

        position: absolute;
        top: 63px;
        left: 156px;
        z-index: -1;

        transform: rotate(5deg);

        @include breakpoint($breakpoint-xs) {
            width: 187px;
            height: 98px;

            top: 73px;
            left: -6px;
        }
    }

    &__btn {
        margin-right: auto;
        order: 3;
    }

    &__zooov {
        width: 168px;
        height: 548px;

        position: absolute;
        top: 160px;
        left: calc(100% - 147px);
        z-index: 2;

        @include breakpoint($breakpoint-xs) {
            width: 156px;
            height: 334px;

            top: 405px;
            left: calc(100% - 126px);
        }
    }
}